* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  min-width: 320px;
}

body._lock{
  overflow: hidden;
}

body._lock::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
  pointer-events: none;
}

html {
  scroll-behavior: smooth;
}